@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@400;700&display=swap);
.HeroCard {
  --width: 240px;
  --padding: 10px;
  display: flex;

  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;

  width: calc(var(--width) + var(--padding));

  margin: 0 20px 20px;
  padding: var(--padding);
}

.HeroCard-img {
  width: var(--width);
  height: var(--width);
  margin-bottom: 10px;

  border-bottom: 5px solid red;

  object-fit: cover;
}

.HeroCard-info {
  display: flex;
  justify-content: space-between;
}

.HeroCard-name {
  font-size: 20px;
  font-weight: bold;
  color: var(--font-color-title);
}

.HeroCard-fav {
  background: none;
  border: none;
}

.HeroCard-fav:hover {
  cursor: pointer;
}

.HeroesList-cards {
  display: flex;

  flex-wrap: wrap;
  justify-content: center;
}



.Header {
  margin-top: 35px;

  text-align: center;
}

.Header-logo {
  cursor: pointer;
}

.Header-title {
  margin: 17px 0 0;

  color: var(--font-color-title);
  font-size: 32px;
}

.Header-subtitle {
  margin: 7px 0;

  font-size: 14px;
  font-weight: normal;
}



.Search {
  width: 70%;
  --height: 4rem;
  margin: 0 auto;
  padding: 12px;

  border-radius: calc(var(--height) / 2);
  background-color: var(--color-wisp-pink);
}

.Search-label {
  display: flex;
}

.Search-inputButton {
  margin-right: 20px;

  border: none;
  background: none;
}

.Search-inputIcon {
  width: auto;
  height: calc(var(--height) / 2);
}

.Search-input {
  flex-grow: 1;

  height: calc(var(--height) - 12px);

  border: none;
  background: none;

  color: var(--font-color-input);
  font-size: calc(var(--height) / 2);
}

.App {
  margin: 0;
}

:root {
  --color-tundora: #404040;
  --color-gray: #8c8c8c;
  --color-silver: #b9b9b9;
  --color-scarlet: #ff1510;
  --color-wisp-pink: #fdecec;
  --color-frostee: #e7f6e7;
  --font-color-title: var(--color-tundora);
  --font-color-text: var(--color-gray);
  --font-color-info: var(--color-silver);
  --font-color-input: var(--color-scarlet);
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;

  color: #8c8c8c;

  color: var(--font-color-text);
  font-family: "Work Sans", sans-serif;
}

